.meal {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #ccc;
}

.meal h3 {
  margin: 0 0 0.25rem 0;
}

.description {
  font-style: italic;
}

.price {
  margin-top: 0.25rem;
  font-weight: bold;
  color: #ad5502;
  font-size: 1.25rem;
}
@media (max-width: 650px) {
  .meal {
    justify-content: flex-end;
    flex-direction: column;
  }
  .meal button{
    margin-top: 10px;
  }
  .meal p{
    margin-top: 8px;
  }
}
